import React from 'react';
import s from "./ProfileImageBlock.module.css"
import userPhoto from "../../assets/images/user.jpg";
import Preloader from "../../assets/images/preloader/Preloader";

const ProfileImageBlock = (props) => {
    if (!props.userProfile) {
        return <Preloader />
    }

    const activateEditMode = () => {
        props.setEditMode(true)
    }

    const onMainPhotoSelected = (event) => {
        if (event.target.files.length) {
            props.savePhoto(event.target.files[0])
        }
    }

    return (
        <div className={s.profileImageBlock}>
            <div className={s.pagePhoto}>
                {props.isOwner
                    ?
                    <label className={s.customFileUpload}>
                        <div className={s.userImgContainer}>
                            <img src={props.userProfile.photos.small !== null ? props.userProfile.photos.small : userPhoto}
                                 alt=""/>
                            <input type={"file"} onChange={onMainPhotoSelected}/>
                        </div>
                    </label>
                    : <div className={s.userImgContainer}>
                        <img src={props.userProfile.photos.small !== null ? props.userProfile.photos.small : userPhoto}
                             alt=""/>
                    </div>
                }
            </div>
            <div className={s.editButton}>
                {props.isOwner && <button onClick={activateEditMode}>Редактировать</button>}
            </div>
        </div>
    );
}

export default ProfileImageBlock;
