import s from './AddPostForm.module.css';
import React from "react";
import {Field, reduxForm} from "redux-form";
import UserMiniImg from "../UserMiniImg/UserMiniImg";

const AddPostForm = (props) => {
    return (
        <form className={s.addPostContainer} onSubmit={props.handleSubmit}>
            <div className={s.fieldContainer}>
                <div className={s.fieldUserImg}>
                    <UserMiniImg {...props}/>
                </div>
                <Field className={s.item + " " + s.postField} placeholder='Что у вас нового?' component={'textarea'} name={'newPostText'}/>
            </div>
            <div className={s.buttonContainer}>
                <button className={s.item}>Опубликовать</button>
            </div>
        </form>
    );
}

const AddPostReduxForm = reduxForm({form: 'addPost'})(AddPostForm)

export default AddPostReduxForm;
