import s from './ProfileInfo.module.css';
import DescriptionBlock from "./DescriptionBlock/DescriptionBlock";
import React, {useState} from "react";
import ProfileImageBlock from "../ProfileImageBlock";

const ProfileInfo = (props) => {
    const [editMode, setEditMode] = useState(false)

    return (
        <div className={s.profileInfo}>
            <ProfileImageBlock {...props} userProfile={props.userProfile} editMode={editMode} setEditMode={setEditMode}/>
            <DescriptionBlock {...props} userProfile={props.userProfile} editMode={editMode} setEditMode={setEditMode}/>
        </div>
    )

}

export default ProfileInfo;
