import s from './DescriptionBlock.module.css';
import React, {useState} from "react";
import Preloader from "../../../../assets/images/preloader/Preloader";
import ProfileStatus from "./ProfileStatus/ProfileStatus";
import DescriptionBlockReduxForm from "./DescriptionBlockForm";
import AddPostContainer from "../../AddPost/AddPostContainer";
import MyPostsContainer from "../../MyPosts/MyPostsContainer";

const DescriptionBlock = (props) => {
    if (!props.userProfile) {
        return <Preloader/>
    }

    const onSubmit = (formData) => {
        props.saveProfile(formData)
            .then(() => {
                props.setEditMode(false)
            })
    }

    return (
            <div className={s.descriptionBlock}>
                <div className={s.userInfo}>
                    <div className={s.userName}>
                        <h3>{props.userProfile.fullName}</h3>
                        <ProfileStatus status={props.status} {...props}/>
                        <div className={s.profileDataBLock}>
                            {props.editMode
                                ?
                                <DescriptionBlockReduxForm initialValues={props.userProfile}
                                                           userProfile={props.userProfile}
                                                           onSubmit={onSubmit}/>
                                : <ProfileData {...props}/>
                            }
                        </div>
                    </div>
                </div>
        </div>
    );
}

const ProfileData = (props) => {
    return (
        <div>
            <div>
                <div className={s.profileDataItem}>
                    <div className={s.profileDataItemKey}>
                        Имя:
                    </div>
                    <div className={s.profileDataItemValue}>
                        {props.userProfile.fullName}
                    </div>
                </div>
                <div className={s.profileDataItem}>
                    <div className={s.profileDataItemKey}>
                        Ищу работу:
                    </div>
                    <div className={s.profileDataItemValue}>
                        {props.userProfile.lookingForAJob ? "Да" : "Нет"}
                    </div>
                </div>
                {props.userProfile.lookingForAJob &&
                <div className={s.profileDataItem}>
                    <div className={s.profileDataItemKey}>
                        Описание навыков:
                    </div>
                    <div className={s.profileDataItemValue}>
                        {props.userProfile.lookingForAJobDescription}
                    </div>
                </div>
                }
                <div className={s.profileDataItem}>
                    <div className={s.profileDataItemKey}>
                        Обо мне:
                    </div>
                    <div className={s.profileDataItemValue}>
                        {props.userProfile.aboutMe}
                    </div>

                </div>
                <div>
                    <div className={s.profileDataItemValue}>
                        {Object.keys(props.userProfile.contacts).map((key) => {
                            return <Contact key={key} contactTitle={key}
                                            contactValue={props.userProfile.contacts[key]}/>
                        })}
                    </div>
                </div>
            </div>
        </div>
    )
}

const Contact = ({contactTitle, contactValue}) => {
    return <div className={s.contact + " " + s.profileDataItem}>
        <div className={s.profileDataItemKey}>
            {contactTitle}:
        </div>
        {contactValue}
    </div>
}

export default DescriptionBlock;
